a:link {
    /* Applies to all unvisited links */
    text-decoration: none;
    font-weight: normal;
    color: rgba(255, 255, 255, 0.8);
}

a:visited {
    /* Applies to all visited links */
    text-decoration: none;
    font-weight: normal;
    color: rgba(255, 255, 255, 0.8);
}

a:hover {
    /* Applies to links under the pointer */
    color: #fff;
    text-underline: none;
}

a:active {
    /* Applies to activated links */
    text-decoration: none;
    font-weight: normal;
    color: rgba(255, 255, 255, 0.8);
}

.Card {
    width: 20%;
    height: 150px;
    color: rgba(255, 255, 255, 0.8);
    margin-top: 3px;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    position: relative;
    padding-top: 10px;
    -webkit-box-shadow: 0 0 0 0 rgba(0, 0, 0, 0.2), 0 3px 10px 0 rgba(0, 0, 0, 0.19);
            box-shadow: 0 0 0 0 rgba(0, 0, 0, 0.2), 0 3px 10px 0 rgba(0, 0, 0, 0.19);
    border-radius: 2px;
    font-weight: 100;
}

.Card-purple {
    background-color: #9464d1;
}

.Card-orange {
    background-color: #e6642c;
}

.Card-blue {
    background-color: #4f89db;
}

.Card-indigo {
    background-color: #7f7ace;
}

.Card-title {
    margin: 10px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
        justify-content: space-between;
    -ms-flex-align: start;
        align-items: flex-start;
    height: 100%;
}

.Card-name {
    text-align: left;
}

.Card-discussions {
    font-size: 0.6em;
    color: rgba(255, 255, 255, 0.50);
    min-width: 50%;
}

.Card-label {
    background-color: rgba(255, 255, 255, 0.20);
    width: 100%;
    height: 50px;
    position: absolute;
    bottom: 0;
    left: 0;
    font-size: 0.75em;
    padding-top: 10px;
    border-radius: 0 0 2px 2px;
}

.Card:hover {
    -webkit-box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
            box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    cursor: pointer;
}


